import React from 'react';
import PropTypes from 'prop-types';
import Pagination from './Pagination/Pagination';
import FilmItem from './FilmItem/FilmItem';
import Loader from '../Loader/Loader';
import styles from './FilmsWrapper.module.css';

const FilmsWrapper = ({
  isLoading,
  films,
  error,
  pages,
  query,
  addFilmToFavorites,
  markAsWatched,
  favoritesList,
  fetchFilms,
  watchedList,
  deleteFromFavorites,
  deleteFromWatched,
  setActivePage,
}) => {
  if (error) {
    // eslint-disable-next-line no-console
    console.log(error);
  }
  return (
    <section className={styles.filmsWrapper}>
      {' '}
      {isLoading && <Loader />}{' '}
      {films.length > 0 &&
        films.map(film => (
          <FilmItem
            key={film.id}
            film={film}
            addFilmToFavorites={addFilmToFavorites}
            markAsWatched={markAsWatched}
            favoritesList={favoritesList}
            watchedList={watchedList}
            deleteFromFavorites={deleteFromFavorites}
            deleteFromWatched={deleteFromWatched}
          />
        ))}{' '}
      {films.length > 0 && (
        <Pagination
          setActivePage={setActivePage}
          pages={pages}
          query={query}
          fetchFilms={fetchFilms}
        />
      )}{' '}
    </section>
  );
};
FilmsWrapper.propTypes = {
  isLoading: PropTypes.bool.isRequired,
  fetchFilms: PropTypes.func.isRequired,
  films: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  error: PropTypes.string.isRequired,
  pages: PropTypes.shape({
    totalPages: PropTypes.number.isRequired,
  }).isRequired,
  query: PropTypes.string.isRequired,
  favoritesList: PropTypes.arrayOf().isRequired,
  watchedList: PropTypes.arrayOf().isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
  addFilmToFavorites: PropTypes.func.isRequired,
  markAsWatched: PropTypes.func.isRequired,
  setActivePage: PropTypes.func.isRequired,
};
export default FilmsWrapper;
