import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import styles from './FilmItem.module.css';

const FilmItem = ({
  film,
  addFilmToFavorites,
  markAsWatched,
  favoritesList,
  watchedList,
  deleteFromFavorites,
  deleteFromWatched,
}) => {
  const addToFavorites = () => {
    addFilmToFavorites(film);
  };
  const addToWatched = () => {
    markAsWatched(film);
  };
  const deleteChosenFilmFromFavorites = () => {
    deleteFromFavorites(film.id);
  };
  const deleteChosenFilmFromWatched = () => {
    deleteFromWatched(film.id);
  };
  return (
    <div className={styles.film}>
      <NavLink to={`/${film.title}/${film.id}`}>
        <img
          src={
            film.poster_path
              ? `https://image.tmdb.org/t/p/w200/${film.poster_path}`
              : 'https://www.viser.edu.rs/images/no_photo.jpeg'
          }
          alt="film poster"
        />
      </NavLink>{' '}
      <div>
        <h3 className={styles.title}> {film.title} </h3>{' '}
        {film.vote_average && (
          <p className={styles.vote}>{film.vote_average}</p>
        )}{' '}
        <p> {film.overview || 'no overview'} </p>{' '}
        {favoritesList.find(
          chosenFilm => Number(chosenFilm.id) === Number(film.id),
        ) ? (
          <button type="button" onClick={deleteChosenFilmFromFavorites}>
            Delete from fav{' '}
          </button>
        ) : (
          <button type="button" onClick={addToFavorites}>
            Add to fav{' '}
          </button>
        )}{' '}
        {watchedList.find(
          chosenFilm => Number(chosenFilm.id) === Number(film.id),
        ) ? (
          <button type="button" onClick={deleteChosenFilmFromWatched}>
            Delete from watched{' '}
          </button>
        ) : (
          <button type="button" onClick={addToWatched}>
            Add to watched{' '}
          </button>
        )}{' '}
      </div>{' '}
      <button type="button">
        <NavLink to={`/${film.title}/${film.id}`}> Show more </NavLink>{' '}
      </button>{' '}
    </div>
  );
};
FilmItem.propTypes = {
  film: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    overview: PropTypes.string.isRequired,
    vote_average: PropTypes.number.isRequired,
    poster_path: PropTypes.string.isRequired,
  }).isRequired,
  favoritesList: PropTypes.arrayOf().isRequired,
  watchedList: PropTypes.arrayOf().isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
  addFilmToFavorites: PropTypes.func.isRequired,
  markAsWatched: PropTypes.func.isRequired,
};

export default FilmItem;
